<template>
    <div>
        <canvas id="barChart" width="100" height="106"> 
        </canvas>
    </div>
</template>

<script>
    import Chart from 'chart.js/auto';

    export default{
        name: 'Bar-chart-component',
        mounted() {
            console.log('comp is mounted')
            const ctx = document.getElementById('barChart');
            // The bar chart
            const barChart = new Chart(ctx, {
            type: 'bar',
            data: {
                labels: ['2018', '2019', '2020', '2021', '2022'],
                datasets: [{
                    label: '# of app downloads per year',
                    data: [700, 450, 500, 800, 950],
                    backgroundColor: [
                        'rgba(12, 105, 87, 1)',
                        'rgba(54, 162, 235, 1)',
                        'rgba(247, 255, 63, 1)',
                        'rgba(75, 192, 192, 1)',
                        'rgba(124, 186, 59, 1)',
                    ],
                    borderColor: [
                        'rgba(12, 105, 87, 1)',
                        'rgba(54, 162, 235, 1)',
                        'rgba(247, 255, 63, 1)',
                        'rgba(75, 192, 192, 1)',
                        'rgba(124, 186, 59, 1)',
                    ],
                    borderWidth: 2
                }]
            },
            options: {
                scales: {
                    y: {
                        beginAtZero: true
                    }
                }
            }
        });
        barChart;
        }
    }
</script>
<style scoped>
    div{
        background-color: whitesmoke;
        border-radius: 15px;
        margin: 10px;
        padding:10px;
    }
    
</style>