<template>
    <div>
        <h6>{{polarAreaLabel}}</h6>
        <canvas id="polarArea" width="100" height="100"></canvas>
    </div>
</template>

<script>
    import Chart from 'chart.js/auto';
    export default {
        name: 'Polar-area-chart-component',
        props: {
            polarAreaLabel: '',
        },
        mounted() {
        const ctx = document.getElementById('polarArea');

        const data = {
            labels: [
                'Slovenia',
                'Serbia',
                'Hungary',
                'America',
                'Germany'
            ],
            datasets: [{
                data: [30, 18, 20, 24, 8],
                backgroundColor: [
                'rgb(124, 186, 59)',
                'rgb(12, 105, 87)',
                'rgb(247, 255, 63)',
                'rgb(201, 203, 207)',
                'rgb(54, 162, 235)'
                ],
                hoverOffset: 10
            }]
            };

        const polarArea = new Chart(ctx, {
            type: 'polarArea',
            data: data,
            options: {}
        });
        polarArea;

        }
    }
</script>

<style scoped>
    div{
        background-color: whitesmoke;
        border-radius: 15px;
        margin: 10px;
        padding:10px;
    }
    h6{
        color: grey;
    }
</style>