<template>
    <div>
        <canvas id="lineChart" width="100" height="30"></canvas>
    </div>
</template>

<script>
    import Chart from 'chart.js/auto';
    export default {
        name: 'Line-chart-component',
        mounted() {
        const ctx = document.getElementById('lineChart');

        const labels = ['January', 'February', 'March', 'April', 'May', 'June', 'July'];
            const data = {
            labels: labels,
            datasets: [{
                label: '#of button clicks per month',
                data: [642, 244, 368, 599, 544, 144, 766],
                fill: false,
                borderColor: 'rgb(12, 105, 87)',
                tension: 0.2
            }]
            };

        const lineChart = new Chart(ctx, {
            type: 'line',
            data: data,
        });
        lineChart;

        }
    }

</script>
<style scoped>
    div{
        background-color: whitesmoke;
        border-radius: 15px;
        margin: 10px;
        padding:10px;
    }
</style>