<template>
    <div>
        <h6>{{pieTitle}}</h6>
        <canvas id="pieChart" width="100" height="100"></canvas>
    </div>
</template>

<script>
    import Chart from 'chart.js/auto';
    export default {
        name: 'Pie-chart-component',
        props: {
            pieTitle: '',
        },
        mounted() {
        const ctx = document.getElementById('pieChart');

        const data = {
            labels: [
                'Gifts',
                'Spin',
                'Extra coins',
                'Extra tickets'
            ],
            datasets: [{
                data: [756, 612, 800, 463],
                backgroundColor: [
                'rgb(124, 186, 59)',
                'rgb(247, 255, 63)',
                'rgb(12, 105, 87)',
                'rgb(1, 178, 255)'
                ],
                hoverOffset: 10
            }]
        };

        const pieChart = new Chart(ctx, {
            type: 'doughnut',
            data: data,
        });
        pieChart;

        }
    }

</script>

<style scoped>
    div{
        background-color: whitesmoke;
        border-radius: 15px;
        margin: 10px;
        padding:10px;
    }
    h6 {
        color:grey;
    }
</style>